import { connect, useDispatch } from 'react-redux';

import cls from './Company.module.scss';
import { useCallback, useRef, useEffect, useState } from 'react';
import { setMessageBar, setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import { FLOWHERO, uncachedUrl } from '../../../shared/utility';
import { useNavigate, useParams } from 'react-router-dom';
import { getCompanyDetailsUsingSlug } from '../../../services/company.service';
import { first } from 'rxjs';
import { isMobile } from 'react-device-detect';
import { Button } from '../../common';
import CustomBasketball from '../../Custom/CustomBasketball';

import { Paint } from '../../common/UI/Paint/Paint';
import { getAlComponentsByModuleIdPublic } from '../../../services/module.service';
import MenuBar, { TopMenu } from '../../TopBar/Navigation/NavigationItems/MenuBar';

const CompanyHome = props => {
	const { edit = false, onClose = () => { }, components } = props;
	const dispatch = useDispatch();
	const [companyData, setCompanyData] = useState(JSON.parse(localStorage.getItem('companyData')) ?? { 'name': '' });
	const { slug } = useParams();

	const navigate = useNavigate();
	document.title = `Company | ${slug}`;

	useEffect(() => {

		if (!companyData?.display_name) {
			dispatch(setShowSideMenu(false));
			dispatch(setShowMenu(false));
			getCompanyDetailsUsingSlug(slug)
				.pipe(first())
				.subscribe({
					next: data => {
						dispatch(setMessageBar({ show: false, message: '' }));
						localStorage.setItem('companyData', JSON.stringify(data));
						setCompanyData(data);
						if (!data?.company_standards?.hbanner) {
							navigate('/login')
						}
					},
				});
		}
	}, [companyData]);

	// Custom Pages
	if (companyData?.id === '543c31fa-d480-4538-99b7-b055e740ad24') {
		return <CustomBasketball />
	} 

	return ((!isMobile && companyData.company_standards?.web) ? <div style={{ width: '100%', marginTop: '-5px' }}>
		<TopMenu companyData={companyData} />
		<Paint data={companyData.company_standards?.web} control={edit} visible={true} oonClose={onClose} onUpdate={e => { }}>

			<div style={{ display: 'flex', position: 'fixed', top: '0', left: '0', justifyContent: 'space-between', width: '100%', padding: '10px' }}>
				{companyData.company_standards?.web ? <div /> : FLOWHERO('26px', 'black', 'bold')}
				
			</div>
		</Paint>
	</div>
		:
		<div
			className={cls.companyHome}
			style={{

				height: '100vh',
				backgroundImage: companyData?.company_standards?.hbanner ? `url(${companyData?.company_standards?.hbanner})` : ``,
				backgroundColor: 'white',
				backgroundSize: 'scale-down',
				backgroundPosition: 'left',
				padding: '20px'
			}}
		>


			<div style={{ padding: '10px 10px', backgroundColor: 'rgba(0, 0, 0, .3)', bbackgroundColor: 'var(--app-color-gray-dark)', borderRadius: '3px', width: '100%', alignContent: 'center' }}>

				<div style={{ fontSize: isMobile ? '20px' : '14px', fontWeight: 'normal', fontFamily: 'var(--app-text-mobile-font)', color: 'white' }}>
					{companyData?.display_name?.toUpperCase()}
					{FLOWHERO('14px', 'white', 'bold')}
				</div>

			</div>
			<div className={cls.centerDiv} style={{ flexDirection: 'column', backgroundColor: 'transparent' }}>
				<Button title={'LOGIN'} clicked={() => navigate('/login')}
					style={{ width: '200px', }}
					titleStyle={{
						fontFamily: 'var(--app-text-mobile-font)',
						color: 'black',
						fontWeight: 'bold',
						fontSize: '16px',
						letterSpacing: '0.8px'
					}}
					light
				/>

				{companyData?.core_values?.singleUser &&

					<Button title={'SIGN UP'} clicked={() => {
						dispatch(setMessageBar({ show: false, message: '' }));
						navigate('/user');
					}}
						style={{ width: '200px', }}
						titleStyle={{
							fontFamily: 'var(--app-text-main-font)',
							color: 'black',
							fontWeight: 'bold',
							fontSize: '16px',
							letterSpacing: '1.5px'

						}}
						light
					/>
				}
			</div>




		</div>



	);
};

const mapStateToProps = store => {
	const {
		authenticatedUser: { position },
		authenticatedUser,
	} = store.authReducer;
	const { user } = store.sessionReducer;

	const { companyData, companyPositionsArray, allTasks, allCompletedTasks, allCompletedTasksPaginated, companyUsersArray, components } = store.flowReducer;
	return {
		companyData,
		companyPositionsArray,
		position,
		allTasks,
		allCompletedTasks,
		allCompletedTasksPaginated,
		user,
		companyUsersArray,
		authenticatedUser,
		components
	};
};


export default connect(mapStateToProps)(CompanyHome);
